.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
    transition: all 0.2s linear;
    border-radius: 5px;
}

.square:focus {
    outline: none;
}

.square:hover {
    background: #b1ecec;
}
